<template>
	<div id="login">
		<div class="logo"><img src="../assets/log.png"/></div>
		<input type="number" placeholder="用户名" autocomplete="off" v-model="username"/>
		<input type="password" placeholder="密码" v-model="pwd"/>
		<button @click="btn">登陆</button>
		<div class="res">
		<a href="javascript:;">注册</a>
		<a href="javascript:;">忘记密码</a>
		</div>
	</div>
</template>

<script>
	import car from '@/car.js'; 
	export default{
		name:'User',
		data(){
			return{
				username:'',
				pwd:''
			}
		},
		methods:{
			btn(){
				if(this.username==123&&this.pwd==123){
					this.$store.commit('chengedPersonInte',car);
					this.$router.push('/index')
				}else{
					alert('账号或密码错误')
				}
			}
		}
	}
</script>

<style>
	#login{
		width: 100vw;
		height: 518px;
		padding-top: 72px;
		padding-left: 40px;
		background: url(../assets/login_back.png);
		margin: 0 auto;
	}
	.logo{
		width: 80px;
		height: 80px;
		margin: 0 121px 72px 81px;
	}
	#login input{
		width: 240px;
		height: 40px;
		font-size: 14px;
	}
	#login button{
		width: 240px;
		height: 40px;
		background: #387ef5;
		border-radius: 10px;
		border: none;
		outline: none;
		color: #fff;
		margin-top: 50px;
	}
	.res{
		width: 240px;
		overflow: hidden;
	}
	.res a{
		font-size: 12px;
		color: #551a8b;
		line-height: 78px;
		text-decoration: none;
	}
	.res a:first-child{
		float: left;
	}
	.res a:last-child{
		float: right;
	}
</style>


