<template>
	<div class="top">
		<span v-show="isShow" @click="back">返回</span>
		<span class="info">{{tittle}}</span>
	</div>
</template>
     
<script>
	export default{
     	name:'Head',
     	props:['tittle','isShow'],
     	methods:{
     		back(){
     			this.$store.commit('cfTrue');
     			this.$router.go(-1);
     		}
     	}
     }
</script>

<style>
	.top{
		width: 100vw;
		height: 40px;
		border-bottom:1px solid #dddddd;
	}
	.top span{
		line-height: 40px;
	}
	.top span:last-of-type{
		float: right;
		margin-right: 140px;
	}
</style>


