<template>
	<div id="app">
		<router-view></router-view>
		<ul class="navs" v-if="navS">
			<router-link to="/index" tag="li">首页</router-link>
			<router-link to="/classList" tag="li">课程列表</router-link>
			<router-link to="/myClass" tag="li">我的课程</router-link>
			<router-link to="/user" tag="li">个人中心</router-link>
		</ul>
	</div>
</template>

<script>
export default {
		name: "App",
		computed:{
			navS(){
				return this.$store.state.onoff;
			}
		}
	}
</script>

<style>
	* {
		margin: 0;
		padding: 0;
	}
	#app .navs {
		position: fixed;
		left: 0;
		bottom: 0;
		overflow: hidden;
		width: 100%;
		height: 50px;
		background: #fff;
		margin: 0;
	}
	
	#app .navs li {
		float: left;
		list-style: none;
		height: 50px;
		width: 25%;
		text-align: center;
		line-height: 50px;
	}
	
	#app .router-link-active {
		background: darkgoldenrod;
		color: hotpink;
	}
</style>


