import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);

//写状态管理
let store = new Vuex.Store({
	state:{
		onoff:true,
		personInte:''
	},
	mutations:{
		cfFalse(state){
			state.onoff = false
		},
		cfTrue(state){
			state.onoff = true
		},
		chengedPersonInte(state,obj){
			state.personInte = obj
		}
	}
})
export default store;


