<template>
	<div id="info">
	    <top tittle="学习页面" :isShow="true"></top> 
	    <video width="100%" height="178">	
	    </video>
	    <ul class="info_tab">
	    	<li>视频</li>
	    	<li>评价</li>
	    	<li>课程详情</li>
	    </ul>
	    <div class="info_text">
	    	<h5>课程名称：{{info.name}}</h5>
	    	<h5>课程价格：{{info.price}}</h5>
	    	<p>课程内容：{{info.describe}}</p>
	    </div>
	    <div class="vedioList">
				<h5>视频列表</h5>
				<div></div>
		</div>
		<div class="pay">
            <div class="evaluate">
            	<span class="glyphicon glyphicon-pencil"></span>
            	<p>评价</p>
            </div>
            <div class="buy">
            	<span class="glyphicon glyphicon-shopping-cart"></span>
            	<p>购买</p>
            </div>
        </div>
	</div>
</template>

<script>
	import top from '@/components/header/head';
	import arr from '@/Array.js'
	export default{
		name:'info',
		components:{
			top
		},
		data(){
			return{
				info:{}
			}
		},
		created(){
			this.info =arr.filter((item)=>{
				return item.id == this.$route.query.id
			})[0],
			//调用onoff回调函数，控制菜单显示隐藏
			this.$store.commit('cfFalse')
		}
	}
</script>

<style>
	#info{
		padding: 0 5px;
	}
	video{
		background: #000;
	}
	.info_tab{
		overflow: hidden;
		width: 97vw;
	}
	.info_tab li{
		list-style: none;
		float: left;
		width: 50%;
		height: 40px;
		text-align: center;
		line-height: 40px;
		border: 1px solid #999;
		box-sizing: border-box;
	}
	.info_tab li:last-of-type{
		width: 100%;
		text-align: left;
		font-weight: 400;
	}
	.pay{
		width: 100vw;
		height: 60px;
		border-top: 1px solid #999999;
		position: fixed;
		bottom: 0;
		left: 0;
	}
	.pay p{
		text-align: center;
		line-height: 20px;
	}
	.pay .glyphicon{
		margin-left: 23vw;
		line-height: 30px;
	}
	.pay div{
		float: left;
		width: 50vw;
	}
	.vedioList>div{
		width: 100vw;
		height: 300px;
	}
	.vedioList>h5{
		line-height: 35px;
		border: 1px solid #999;
	}
</style>


