<template>
	<div id="myclass">
		<top tittle="我的课程" :isShow="false"></top>
		<ul class="my_car">
			<li v-for="item in myclass">
				<img :src="item.image"/>
				<div class="describe">
					<h5>{{item.name}}</h5>
					<span>课程价格：{{item.price}}</span>
					<p>{{item.describe|str}}......</p>
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
	import top from '@/components/header/head';
	export default{
		name: 'myclass',
		components:{
			top
		},
		data(){
			return{
				myclass:''
			}
			
		},
		created(){
			let clas = this.$store.state;
			this.myclass = clas.personInte.shoping;
			//console.log(this.myclass)
		},
		filters:{
			str(index){
				return index.substring(0,35)
			}
		}
	}
</script>

<style>
	#myclass{
		background: saddlebrown;
	}
	.my_car li{
		margin-top: 20px;
		list-style: none;
		width: 100vw;
		height: 130px;
		background: antiquewhite;
		overflow: hidden;
	}
	.my_car li img{
		width: 40vw;
		height: 130px;
		float: left;
	}
	.describe {
		float: right;
		width: 55vw;
	}
	.describe h5{
		color: #FF69B4;
	}
</style>


