<template>
	<div id="home">
		<top tittle="首页" :isShow="false"></top>
		<swiper :options="option">
			<swiperSlide v-for="(item,index) in list" class="item" :key="index">
				<img :src="item.image"/>
			</swiperSlide>

			<div class="swiper-pagination" slot="pagination"></div>
		</swiper>
		<ul class="lists">
			<li v-for="item in list" @click="info(item.id)">
				<img :src="item.image"/>
				<h3>{{item.name}}</h3>
				<p>{{item.describe|sub}}</p>
			</li>
		</ul>
	</div>
</template>

<script>
	//引入sweiper
	import { swiper, swiperSlide } from 'vue-awesome-swiper';
	import list from '@/Array.js';
	import top from '@/components/header/head';
	export default {
		name: 'index',
		components: {
			swiper,
			swiperSlide,
			top
		},
		data() {
			return {
				option: {
					pagination: { // 是否要圆点
						el: '.swiper-pagination'
					},
					loop: true, // 无缝轮播
					autoplay: {
				    delay: 3000,
				    stopOnLastSlide: false,
				    disableOnInteraction: true,
				    },
				},
				list
			}
		},
		methods:{
			info(id){
				this.$router.push({
					path:'/info',
					query:{
						id:id
					}
				})
			}
		},
		filters:{
			sub(value){
				return value.substring(0,10)
			}
		}
		/*watch:{
			$route:{
				deep:true,
				handler:function(){
					console.log('我跳到首页了');
				}
			}
		},
		created(){
			
			this.list = data;
		}*/
	}
</script>

<style>
*{
	margin: 0;
	padding: 0;
}
.swiper-slide{
	width: 100%;
	height: 152px;
}
.swiper-slide img{
	width:100%;
	height: 100%;
	vertical-align: top;
}
.lists{
	width: 100vw;
	margin: 0 auto;
}
.lists li{
	list-style: none;
	float: left;
	width: 50vw;
	height: 200px;
	margin-bottom: 20px;
}
.lists li img{
	height: 130px;
	width: 90%;
}
</style>


