<template>
	<div id="classList">
		<top tittle="课程列表" :isShow="false"></top>
		<div class="content">
			<div class="search">
				<input type="text" placeholder="搜索课程" v-model="search"/>
				<button type="button" class="btn btn-success" @click="btn"><span class="glyphicon glyphicon-search"></span></button>
			</div>
			<ul class="cl_text">
				<li v-for="item in showList" @click="btnList(item.id)"> 
					<img :src="item.image" />
					<div class="cl_title">
						<h5>{{item.name}}</h5>
						<p>{{item.describe|str}}</p>
					</div>
				</li>
			</ul>
		</div>
        
	</div>
</template>

<script>
	import top from '@/components/header/head';
	import data from '@/Array.js';
	export default {
		name: 'classList',
		components: {
			top
		},
		data(){
			return{
				search:'',
				showList:[]
			}
		},
		methods:{
			btn(){
				this.showList = data.filter((item)=>{
					return item.name == this.search;
				})
				//console.log(this.showList)
			},
			btnList(id){
				this.$router.push({
					path:'/info',
					query:{
						id
					}
				})
			}
		},
		filters:{
			str(value){
				return value.substring(0,15);
			}
		}
	}
</script>

<style>
	.content{
		width: 100vw;
		padding: 5px 5px;
	}
	.search input {
		height: 34px;
		width: 82vw;
	}
	.cl_text {
		width: 100vw;
	}
	.cl_text li{
		list-style: none;
		width: 100vw;
		overflow: hidden;
	}
	.cl_text li img{
		width: 40vw;
		height: 100px;
		float: left;
	}
	.cl_title{
		float: right;
		width: 50vw;
	}
</style>


